html {
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}

html, body {
    padding: 0;
    margin: 0;
}


html, body, #root {
    width: 100%;
    height: 100%;
}

.topnav-area {
    grid-area: topnav;
    height: 75px;
    width: 100%;
    background: brown;
}

.sidebar-area {
    grid-area: sidebar;
    background: teal;
}

.breadcrumbs-area {
    grid-area: breadcrumbs;
}

.content-area {
    grid-area: content;
    position: relative;
    background: pink;
}

.grid-wrapper {
    width: 100vw;
  height: 100vh;
    display: grid;
    grid-template-columns: 200px calc(100% - 200px);
    grid-template-rows: 75px 75px auto;
    grid-template-areas: 
        "topnav topnav"
        "sidebar breadcrumbs"
        "sidebar content"
}

.player-preview-popup {
    position: absolute;
    right: 0;
    bottom: 0;
    height: 300px;
    width: 300px;
    background: white;
}

.player-iframe {
    border: none;
    height: calc((300px * 56.25) / 100);
}